//
import { Resources } from './Resource.js'

export class ResourceLoader {
  constructor() {
    this.imageCount = 0
    this.audioCount = 0
    //
    this.map = new Map(Resources)
    for (let [key, src] of this.map) {
      let res = null
      if (src.split('.')[1] == 'png' || src.split('.')[1] == 'jpg') {
        this.imageCount++
        // H5imageAPI
        res = new Image()
        // imageAPI
        // res = wx.createImage()
        res.src = src
      } else {
        this.audioCount++
        // H5audioAPI
        res = new Audio()
        // audioAPI
        // res = wx.createInnerAudioContext()
        res.src = src
      }
      this.map.set(key, res)
    }
  }

  // 
  onload(cb) {
    let loadCount = 0
    for (let res of this.map.values()) {
      // thisResourceLoader
      res.onload = () => {
        loadCount++
        if (loadCount >= this.imageCount) {
          cb(this.map)
        }
      }
    }
  }
}